import { TabixIndexedFile } from '@gmod/tabix';
import VcfParser from '@gmod/vcf';
import { BaseFeatureDataAdapter } from '@jbrowse/core/data_adapters/BaseAdapter';
import { fetchAndMaybeUnzipText, shorten2, updateStatus, } from '@jbrowse/core/util';
import { openLocation } from '@jbrowse/core/util/io';
import { ObservableCreate } from '@jbrowse/core/util/rxjs';
import VcfFeature from '../VcfFeature';
export default class VcfTabixAdapter extends BaseFeatureDataAdapter {
    async configurePre(_opts) {
        const vcfGzLocation = this.getConf('vcfGzLocation');
        const location = this.getConf(['index', 'location']);
        const indexType = this.getConf(['index', 'indexType']);
        const filehandle = openLocation(vcfGzLocation, this.pluginManager);
        const isCSI = indexType === 'CSI';
        const vcf = new TabixIndexedFile({
            filehandle,
            csiFilehandle: isCSI
                ? openLocation(location, this.pluginManager)
                : undefined,
            tbiFilehandle: !isCSI
                ? openLocation(location, this.pluginManager)
                : undefined,
            chunkCacheSize: 50 * 2 ** 20,
        });
        return {
            vcf,
            parser: new VcfParser({
                header: await vcf.getHeader(),
            }),
        };
    }
    async configurePre2() {
        if (!this.configured) {
            this.configured = this.configurePre().catch((e) => {
                this.configured = undefined;
                throw e;
            });
        }
        return this.configured;
    }
    async configure(opts) {
        const { statusCallback = () => { } } = opts || {};
        return updateStatus('Downloading index', statusCallback, () => this.configurePre2());
    }
    async getRefNames(opts = {}) {
        const { vcf } = await this.configure(opts);
        return vcf.getReferenceSequenceNames(opts);
    }
    async getHeader(opts) {
        const { vcf } = await this.configure(opts);
        return vcf.getHeader();
    }
    async getMetadata(opts) {
        const { parser } = await this.configure(opts);
        return parser.getMetadata();
    }
    getFeatures(query, opts = {}) {
        return ObservableCreate(async (observer) => {
            const { refName, start, end } = query;
            const { statusCallback = () => { } } = opts;
            const { vcf, parser } = await this.configure(opts);
            await updateStatus('Downloading variants', statusCallback, () => vcf.getLines(refName, start, end, {
                lineCallback: (line, fileOffset) => {
                    observer.next(new VcfFeature({
                        variant: parser.parseLine(line),
                        parser,
                        id: `${this.id}-vcf-${fileOffset}`,
                    }));
                },
                ...opts,
            }));
            observer.complete();
        }, opts.stopToken);
    }
    async getSources() {
        const conf = this.getConf('samplesTsvLocation');
        if (conf.uri === '' || conf.uri === '/path/to/samples.tsv') {
            const { parser } = await this.configure();
            return parser.samples.map(name => ({
                name,
            }));
        }
        else {
            const txt = await fetchAndMaybeUnzipText(openLocation(conf, this.pluginManager));
            const lines = txt.split(/\n|\r\n|\r/);
            const header = lines[0].split('\t');
            const { parser } = await this.configure();
            const metadataLines = lines
                .slice(1)
                .filter(f => !!f)
                .map(line => {
                const [name, ...rest] = line.split('\t');
                return {
                    ...Object.fromEntries(header.slice(1).map((c, idx) => [c, rest[idx] || ''])),
                    name: name,
                };
            });
            const vcfSampleSet = new Set(parser.samples);
            const metadataSet = new Set(metadataLines.map(r => r.name));
            const metadataNotInVcfSamples = [...metadataSet].filter(f => !vcfSampleSet.has(f));
            const vcfSamplesNotInMetadata = [...vcfSampleSet].filter(f => !metadataSet.has(f));
            if (metadataNotInVcfSamples.length) {
                console.warn(`There are ${metadataNotInVcfSamples.length} samples in metadata file (${metadataLines.length} lines) not in VCF (${parser.samples.length} samples):`, shorten2(metadataNotInVcfSamples.join(',')));
            }
            if (vcfSamplesNotInMetadata.length) {
                console.warn(`There are ${vcfSamplesNotInMetadata.length} samples in VCF file (${parser.samples.length} samples) not in metadata file (${metadataLines.length} lines):`, shorten2(vcfSamplesNotInMetadata.map(m => m).join(',')));
            }
            return metadataLines.filter(f => vcfSampleSet.has(f.name));
        }
    }
}
